.frontPage {
  position: absolute;
  box-shadow: 0px 10px 53px 0px rgba(0, 0, 0, 0.3) !important;
  border-radius: 20px;
  /* height: 800px; */
  /* padding: 20px; */
  top: 1vh;
  bottom: 1vh;
  right: 1vh;
  left: 1vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.frontPage__SortingButtons {
  /* position: relative; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #cecece;
  padding: 20px;

  /* margin-bottom: 30px; */
  width: 100%;
}
.frontPage__SortingButtons .MuiButton-root {
  color: rgb(57, 200, 195);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.3) !important;
  /* margin: 4 4px; */
}

