  display: flex;
  /* position: absolute; */
  /* left: 100px; */
  /* bottom: 10vh; */
  /* width: 70%; */
  justify-content: space-evenly;
  background-color: rgb(249, 249, 249);
  height: 100%;
  /* max-width: 100vw; */
  align-items: center;
  /* equivalently margin: top right bottom left; */
  margin-top: 30px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 30px;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.3) !important;
  /* scale: 1 0.5; */
}
